<script>
    import Fa from 'svelte-fa';
    import { faTrash } from '@fortawesome/free-solid-svg-icons';

    export let extraData;
    console.log(extraData);
    let macros = extraData['macro']
    import { update_macro, delete_macro } from './utils.js';
	import Header from './components/header.svelte';
    
    const current_user = typeof extraData['current_user'] == "string" ? extraData['current_user'] : extraData['current_user']['name'];
    const del_macro_func = macro => {
        let resp = delete_macro(macro[0]);
        resp.then(r => {
            if (r.ok)
                macros = macros.filter(mac => mac[0] != macro[0]);
        });
    }
</script>

<main class="index">
    <Header user_info={extraData['current_user']} />
    <div class="banner">
        <h1>Hello {current_user}!</h1>
        <span class="banner__right">
            <a href="/create">
                <h2 class="create_button">Create New Macro</h2>
            </a>
            <a href="/word_listeners">
                <h2 class="create_button">Word Listeners</h2>
            </a>
            <a href="/regioncheck">
                <h2 class="create_button">Get Region</h2>
            </a>
        </span>
    </div>
    {#each macros as macro, index}
        <div class="macro">
            <div class="space-between">
                <a href="/{macro[0]}">
                    <h3>{macro[0]} - {macro[2]}</h3>
                </a>
                <span>
                    Enabled:
                    <input type="checkbox" checked={macro[5]} on:click={() => update_macro('enabled', macro[5] ? 0 : 1, macro[0])}>
                    Delete: 
                    <span class="pointer" on:click={() => del_macro_func(macro)} role="presentation"><Fa icon={faTrash} /></span>
                </span>
            </div>
            <p class="tags">{macro[1]}</p>
        </div>
    {/each}
</main>


